<template>
  <div class ="company">
  <Form ref="formValidate" :model="company" :rules="ruleValidate" :label-width="100">
    <Form-item label="" prop="name">
      <Input v-model="company.name" placeholder=""></Input>
    </Form-item>
    <Form-item label="logo" class="text-left" >
      <uploader :config="uploaderconfig"> </uploader>
      <input type="hidden" v-model="company.logoImg">
    </Form-item>
    <Form-item label="">
      <a :href="murl + company.logoImg" target="_blank" v-if="company.logoImg">
        <img :src="murl+company.logoImg" alt="" class="goodsimgthumb" style = "width:125px;height:125px;">
      </a>
    </Form-item>
    <Form-item label="" prop="type">
      <Select v-model="company.type" placeholder="">
        <Option value="1"></Option>
        <Option value="2"></Option>
        <Option value="3"></Option>
      </Select>
    </Form-item>
    <Form-item label="" >
      <Select v-model="areaOne.id" style="width:33%;" placeholder="" @on-change="change($event,1)" :label-in-value=true>
        <Option v-for = "area in areasOne" :value="area.region_id" :key = "area" >{{area.region_name}}</Option>
      </Select>
      <Select v-model="areaTwo.id" style="width:33%;" placeholder="" :disabled="!areaOne.id" @on-change="change($event,2)" :label-in-value=true>
        <Option v-for = "area in areasTwo" :value="area.region_id" :key = "area">{{area.region_name}}</Option>
      </Select>
      <Select v-model="company.areaId" style="width:33%;" placeholder="" :disabled="!areaTwo.id" @on-change="change($event,3)" :label-in-value=true>
        <Option v-for = "area in areasThree" :value="area.region_id" :key = "area">{{area.region_name}}</Option>
      </Select>
    </Form-item>
    <Form-item label="" prop="address">
      <Input v-model="company.address"></Input>
    </Form-item>
    <Form-item label="" prop="username">
      <Input v-model="company.username"></Input>
    </Form-item>
    <Form-item label="" prop="password">
      <Input v-model="company.password"></Input>
    </Form-item>
    <Form-item label="" prop="phone">
      <Input v-model="company.phone"></Input>
    </Form-item>
    <Form-item label="" prop="email">
      <Input v-model="company.email"></Input>
    </Form-item>
    <Form-item label="" prop="modelCid">
      <Select v-model="company.modelCid" placeholder="">
        <Option v-for = "model in models" :key="model" :value="model.modelCid">{{model.modelName}}</Option>
      </Select>
    </Form-item>
    <Form-item label="" >
      <Input v-model="company.slogan"></Input>
    </Form-item>
    <Form-item label="" >
      <Input v-model="company.fax"></Input>
    </Form-item>
    <Form-item>
      <Button type="primary" @click="handleSubmit('company')"></Button>
      <Button type="ghost" @click="handleReset('company')" style="margin-left: 8px"></Button>
    </Form-item>
  </Form>
  </div>
</template>
<script>
  import uploader from '../Util/Uploader'
  export default {
    data () {
      return {
        company: {
          name: '',
          parentId: '0',
          areaId: '',
          type: '',
          address: '',
          logoImg: '',
          phone: '',
          username: '',
          password: '',
          fax: '',
          email: '',
          modelCid: '',
          slogan: ''
        },
        areaOne: {
          id: null,
          name: ''
        },
        areaTwo: {
          id: null,
          name: ''
        },
        areaThree: {
          id: null,
          name: ''
        },
        areasOne: [],
        areasTwo: [],
        areasThree: [],
        models: [],
        ruleValidate: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          phone: [
            { required: true, message: '', trigger: 'blur' }
          ],
          logoImg: [
            { required: true, message: 'logo', trigger: 'change' }
          ],
          type: [
            { required: true, message: '', trigger: 'change' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' }
          ],
          username: [
            { required: true, message: '', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '', trigger: 'blur' }
          ],
          modelCid: [
            { required: true, message: '', trigger: 'change' }
          ],
          email: [
            { required: true, message: '', trigger: 'blur' },
            { type: 'email', message: '', trigger: 'blur' }
          ]
        },
        uploaderconfig: {
          maxSize: 5120,
          format: ['jpg', 'png', 'jpeg'],
          showUploadList: false,
          parent: 'company',
          child: 'logoImg'
        }
      }
    },
    components: { uploader },
    created () {
      this.getArea(1);
      this.getModel();
    },
    methods: {
      change (e, val) {
        if (val === 1) {
          this.areaOne.name = e.label;
          this.http.get('/api/a/sys/area/getRegion?parentId=' + e.value).then((res) => {
            if (res.success === true) {
              this.areasTwo = res.result;
            }
          });
        } else if (val === 2) {
          this.areaTwo.name = e.label;
          this.http.get('/api/a/sys/area/getRegion?parentId=' + e.value).then((res) => {
            if (res.success === true) {
              this.areasThree = res.result;
            }
          });
        } else {
          this.areaThree.name = e.label;
        }
      },
      handleSubmit () {
        this.company.address = this.areaOne.name + '-' + this.areaTwo.name + '-' + this.areaThree.name + '-' + this.company.address;
        var ai = JSON.parse(JSON.stringify(this.company))
        console.log(ai)
        if (!ai.name) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.areaId) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.type) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.address) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.phone) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.username) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.password) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        if (!ai.modelCid) {
          this.$Notice.info({title: '', desc: ''})
          return false
        }
        this.http.post('/api/a/sys/company/save', this.company).then((res) => {
          if (res.success === true) {
            this.$Message.success('')
          }
        });
      },
      handleReset (name) {
        this.$refs[name].resetFields();
      },
      getModel () {
        this.http.get('/api/a/sys/company/getModelCompanyInfo').then((res) => {
          if (res.success === true) {
            this.models = res.result;
          }
        });
      },
      getArea () {
        this.http.get('/api/a/sys/area/getRegion').then((res) => {
          if (res.success === true) {
            this.areasOne = res.result
          }
        });
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .company
    margin-top 20px
    width 70%
</style>



